  --color-light-gray: #F5F5F5;
  --color-medium-gray: #D9D9D9;
  --color-dark-gray: #1C1B1F;
  --color-charcoal: #030406;
  --color-warning-orange: #FFAD33;
  --color-success-green: #00FF66;
  --color-info-blue: #A0BCE0;
  --color-error-red: #E07575;
  --background: var(--color-pure-white);
  --foreground: var(--color-pure-black);
