  padding: 8px 16px;
  background: transparent;
  border: none;
  cursor: pointer;
  font-size: 18px;
  font-weight: 500;
  transition: background-color 0.2s ease;
}
.quantity-btn:hover {
  background-color: var(--color-light-gray);
}
.quantity-display {
  padding: 8px 24px;
  border-left: 1px solid var(--color-medium-gray);
  border-right: 1px solid var(--color-medium-gray);
  font-weight: 500;
  min-width: 60px;
  text-align: center;
}
.size-selector {
  display: flex;
  gap: 12px;
}
.size-option {
  padding: 8px 12px;
  border: 1px solid var(--color-medium-gray);
  background: transparent;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s ease;
}
.size-option:hover {
  border-color: var(--color-brand-red);
}
.size-option.active {
  background-color: var(--color-brand-red);
  color: var(--color-pure-white);
  border-color: var(--color-brand-red);
}
.color-selector {
  display: flex;
  gap: 12px;
}
.color-option {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
}
.color-option:hover {
  transform: scale(1.1);
}
.color-option.active {
  box-shadow: 0 0 0 2px var(--color-brand-red), 0 0 0 4px var(--color-pure-white);
}
.delivery-info {
  border: 1px solid var(--color-medium-gray);
  border-radius: 8px;
  overflow: hidden;
